"use client";

import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import Link from "next/link";
import { Menu, X } from "lucide-react";
import { useRouter } from "next/navigation";
import { useTheme } from "next-themes";

export default function Nav({ dataUMKM }) {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [active, setActive] = useState("/");
  const router = useRouter();
  const { theme } = useTheme();

  // NAV SCROLL EFFECT
  useEffect(() => {
    const handleScroll = () => {
      const heroHeight = window.innerHeight * 0.55;
      setIsScrolled(window.scrollY > heroHeight * 0.25);
    };

    window.addEventListener("scroll", handleScroll);
    handleScroll();
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // DETECT ACTIVE ROUTE
  useEffect(() => {
    setActive(window.location.pathname);
  }, []);

  return (
    <nav
      className={`
        fixed top-0 left-0 w-full z-[100]
        transition-all duration-300 backdrop-blur-md
        ${isScrolled ? "bg-bg-base/70 shadow-sm" : "bg-transparent"}
      `}
    >
      <div className="max-w-7xl mx-auto flex items-center justify-between px-4 sm:px-6 py-4">
        
        {/* LOGO */}
        <Link
          href="/"
          className="flex items-center gap-2 cursor-pointer"
        >
          <img
            src="/assets/logo.png"
            alt="Gelap Nyawang Logo"
            className="w-9 h-9 object-contain"
          />
          <span className="font-heading text-xl text-text-primary">
            Gelap Nyawang
          </span>
        </Link>

        {/* DESKTOP NAV */}
        <div className="hidden md:flex items-center gap-8">
          {[
            { name: "Beranda", href: "/" },
            { name: "Direktori", href: "/direktori" },
            { name: "Lokasi", href: "#lokasi" },
          ].map((item) => (
            <Link
              key={item.href}
              href={item.href}
              className="relative group text-text-primary font-medium tracking-wide"
            >
              {item.name}

              {/* Underline */}
              <span
                className={`
                  absolute left-0 -bottom-1 h-[1.5px] w-full
                  scale-x-0 group-hover:scale-x-100
                  transition-transform duration-300 origin-left
                  bg-gradient-to-r from-primary to-secondary-light
                  ${active === item.href ? "scale-x-100" : ""}
                `}
              />
            </Link>
          ))}
        </div>

        {/* MOBILE MENU BUTTON */}
        <button
          className="md:hidden text-text-primary focus:outline-none"
          onClick={() => setIsMenuOpen(true)}
        >
          <Menu size={28} />
        </button>
      </div>

      {/* MOBILE SIDEBAR */}
      {isMenuOpen && (
        <motion.div
          initial={{ x: "100%" }}
          animate={{ x: 0 }}
          exit={{ x: "100%" }}
          transition={{ duration: 0.3 }}
          className="fixed top-0 right-0 w-64 h-full bg-bg-base shadow-xl z-[200] p-6"
        >
          <div className="flex justify-between items-center mb-6">
            <span className="font-heading text-xl text-text-primary">
              Menu
            </span>
            <button onClick={() => setIsMenuOpen(false)}>
              <X size={26} className="text-text-primary" />
            </button>
          </div>

          <div className="flex flex-col gap-6">
            {[
              { name: "Beranda", href: "/" },
              { name: "Direktori", href: "/direktori" },
              { name: "Lokasi", href: "#lokasi" },
            ].map((item) => (
              <Link
                key={item.href}
                href={item.href}
                onClick={() => setIsMenuOpen(false)}
                className="text-lg text-text-primary font-medium tracking-wide"
              >
                {item.name}
              </Link>
            ))}
          </div>
        </motion.div>
      )}
    </nav>
  );
}
